
import { Vue, Component, Prop } from "vue-property-decorator";
import Icon from "@/components/UI/Icon.vue";
import {
  getDocumentUrlFromId,
  replaceDocumentWithAnother
} from "@/lib/wordpress/load";
import EditTemplateModal from "@/subapps/manage-templates/EditTemplateModal.vue";
import {
  SupportedPostType,
  Template,
  TemplatesManagementData
} from "@/subapps/manage-templates/types";
import { DocumentMeta } from "@/types/document";
import { configStore, documentStore } from "@/store/store";
import { pipe } from "fp-ts/function";
import { getAllTemplatesAndNeededDataFromWordPress } from "@/subapps/manage-templates/lib/api";
import { fold } from "fp-ts/Either";
import NotificationLogger from "@/lib/notifications/notifications";

@Component({
  components: { EditTemplateModal, Icon }
})
export default class DocumentChooserItem extends Vue {
  @Prop()
  item!: DocumentMeta;

  templates: Template[] = [];
  modalData: { type: "edit" | "add"; data: Template } | false = false;
  loadingData = false;

  templatesManagementData: TemplatesManagementData = {
    conditions: [],
    dynamicFields: [],
    templateTypeCategories: [],
    popupTriggers: null
  };

  get currentDocument() {
    return documentStore.document;
  }

  get templateParentsWithoutTrashed() {
    return [];
  }

  get templatesByStatus() {
    return [];
  }

  get documentUrl() {
    return getDocumentUrlFromId(this.item.id);
  }

  get isBreakdanceTemplate() {
    return (
      this.item.postType.startsWith("breakdance_") &&
      this.item.postType !== "breakdance_block"
    );
  }

  get currentTemplate() {
    return this.templates.find(t => t.id === this.item.id);
  }

  async editTemplate() {
    this.loadingData = true;

    await this.loadNeededData();

    if (!this.currentTemplate) {
      NotificationLogger.log.message("Couldn't load templates data");
      return;
    }

    this.modalData = {
      type: "edit",
      data: this.currentTemplate
    };

    this.loadingData = false;
  }

  async loadNeededData() {
    const type = this.item.postType as SupportedPostType;

    pipe(
      await getAllTemplatesAndNeededDataFromWordPress(type),
      fold(
        () => {
          NotificationLogger.log.message("Couldn't load templates data");
        },
        data => {
          this.templates = data.templates;
          this.templatesManagementData = data.templatesManagementData;
          configStore.setDynamicFields(
            data.templatesManagementData.dynamicFields
          );
          // Necessary for ConditionPreviewRule to display the condition's label instead of the rule slug
          configStore.setConditions(data.templatesManagementData.conditions);
          configStore.setEnvironment(data.environment);
        }
      )
    );
  }

  async replaceCurrentBuilderPage(event: KeyboardEvent) {
    if (event.ctrlKey || event.metaKey) return;

    if (window.Breakdance.initialMode !== "builder") return;

    event.preventDefault();

    this.$emit("click");
    replaceDocumentWithAnother(this.item.id);
  }
}
