
import { Vue, Component, Prop } from "vue-property-decorator";
import DropdownInput from "@/components/Reusable/CustomInputs/DropdownInput.vue";
import Dialog from "@/components/UI/Dialog.vue";
import TextInput from "@/components/Reusable/CustomInputs/TextInput.vue";
import { pipe } from "fp-ts/function";
import { makeWordPressAjaxRequest } from "@/lib/wordpress/ajax";
import * as t from "io-ts";
import { fold } from "fp-ts/Either";

@Component({
  components: { TextInput, Dialog, DropdownInput }
})
export default class DocumentChooserCreateDialog extends Vue {
  @Prop()
  open!: boolean;

  postTitle = "";
  postType = "";

  async create() {
    if (!this.postTitle || !this.postType) return;

    const response = pipe(
      await makeWordPressAjaxRequest({
        action: "breakdance_insert_post",
        friendlyName: "Insert Post",
        dataToPost: {
          postTitle: this.postTitle,
          postType: this.postType
        },
        decoder: t.type({
          postId: t.number
        })
      }),
      fold(
        () => undefined,
        data => data
      )
    );

    this.$emit("close");
    this.$emit("created", this.postTitle);
    console.log(response);
  }

  dropdownOptions = {
    populate: {
      path: "",
      text: "",
      value: "",
      fetchDataAction: "breakdance_get_editable_post_types",
      fetchContextPath: "",
      refetchPaths: []
    }
  };
}
